export default {
  props: {
    percentage: {
      type: Number,
      default: 0
    }
  },
  computed: {
    value() {
      let val = this.percentage.toFixed(1);
      return val;
    },
    waterValue() {
      // 0~137
      let waterVal = 137 - (this.value * 137) / 100;
      return waterVal;
    }
  }
};
